
  .login-form {
    width: 400px;
    margin: 25px auto;
    background-color: #f2f2f2;
    padding: 25px;
  }

  .login-form .errors {
    color: red;
  }

  .login-form input {
    border: 1px solid #ddd;
  }

  .btn-yellow {
    width: 100%;
    background-color: #ecce00;
    color: #333;
    transition: 2s;
  }